.container {
  position: fixed;
  top: 44px;
  bottom: 0;
  width: 300px;
}[dir] .container {
  padding-top: 40px;
}[dir=ltr] .container {
  left: 0;
  padding-left: 30px;
}[dir=rtl] .container {
  right: 0;
  padding-right: 30px;
}

.heading {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-line-pack: center;
      align-content: center;
  font-size: 18px;
  font-weight: 500;
}

[dir] .heading {
  margin: 30px 0 15px 0;
}

[dir=ltr] .heading {
  padding: 0 0 10px 10px;
}

[dir=rtl] .heading {
  padding: 0 10px 10px 0;
}
[dir=ltr] .heading span {
  margin-left: 14px;
}
[dir=rtl] .heading span {
  margin-right: 14px;
}

.list {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  font-size: 15px;
  font-weight: 500;
}

[dir] .item {
  cursor: pointer;
  margin-bottom: 12px;
}
[dir] .item svg {
  margin: 0 8px 0 8px;
}
.item:hover .text {
  color: rgb(33, 150, 243);
}

.link {
  color: #000;
  font-weight: 500;
  text-decoration: none;
}

.link.active .item {
  color: rgb(33, 150, 243);
}

[dir=ltr] .text {
  margin: 4px 0 0 4px;
}

[dir=rtl] .text {
  margin: 4px 4px 0 0;
}



