.container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
}

.content {
  position: fixed;
  top: 44px;
  width: calc(100vw - 300px);
  height: 100vh;
  overflow: scroll;
  overflow-x: hidden;
}

[dir=ltr] .content {
  left: 300px;
}

[dir=rtl] .content {
  right: 300px;
}



